<template>
	<div class="overFlow">
		<el-dialog :visible.sync="dialogVisible" width="80%" top="9vh">
			<img src="../assets/bill.jpg" alt width="100%" />
		</el-dialog>
		<!-- :up="mescrollUp" :down='mescrollDown' -->
		<div>
			<img ref="topImg" @load="onloadimg" src="../assets/tagpng.jpg" alt width="100%" />

			<div ref="toptitle">
				<h3 class="title">北京国际科学传播交流周<br />
				</h3>
			</div>

			<div class="inline-text" ref="toptext">
				<div style="text-align: left;">
					<i class="el-icon-user"></i>
					<span style="color: red;">{{usernum}}</span>
					<span>&nbsp;人正在收看此次图片直播</span>
				</div>
				<div style="text-align: right;" @click="message">
					<!-- <span>活动主办</span> -->
				</div>
			</div>

			<div class="inline-text" ref="toptext" style="    padding-bottom: 13px;">
				<div style="text-align: left;">
					<i class="el-icon-time"></i>
					<span>&nbsp;2019年11月2日-11月10日</span>
				</div>

			</div>
			<div class="inline-text" ref="toptext">
				<div style="text-align: left;">
					<i class="el-icon-location-outline"></i>
					<span>&nbsp;中国·北京科学中心</span>
				</div>
			</div>



			<div ref="toptitle" style="margin-top: 20px;">

				<h3 class="title" style="font-size: 1.0rem;"><i class="el-icon-caret-right"></i>图片直播入口</h3>
			</div>


			<div class="enterBtn">
				<img src="../assets/btn1.jpg" alt="" @click='enter(11)'>
				<img src="../assets/btn2.jpg" alt="" @click='enter(9)'>
				<img src="../assets/btn3.jpg" alt="" @click='enter(1)'>
			</div>

			<Footer></Footer>
		</div>
	</div>
</template>

<script>
	import Footer from "./footer.vue";
	import MescrollVue from "mescroll.js/mescroll.vue";
	const axios = require("axios");
	import qs from "qs";
	//  mescrollUp: {
	//     callback: this.cb,
	//     htmlLoading: '<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载11中..</p>',
	//     htmlNodata: '<p class="upwarp-nodata">-- 没有数据了 --</p>',
	//     onScroll: this.scroll
	//   },
	//   mescrollDown: {
	//     use: false,
	//   },
	export default {
	
		data() {
			return {
				usernum: 0,
				list: [],
				imgdata: [{
					url: "",
					txt: "礼赞共和国  智慧新生活    礼赞共和国  智慧新生活",
					datetime: "2019-08-12 24:24:24",
					likes: "0",
					id: 0
				}],

				dialogVisible: false
			};
		},
		created() {
			this.onlinenum();
			this.reloadNum();
		},
		mounted() {
			// 监听dom渲染完成
			this.$nextTick(function() {
				// 这里fixedHeaderRoot是吸顶元素的ID
				this.$store.state.fixheight += this.$refs.toptitle.offsetHeight;
				console.log(this.$store.state.fixheight);

				this.$store.state.fixheight += this.$refs.toptext.offsetHeight;
				console.log(this.$store.state.fixheight);
			});
		},
		components: {
			Footer,

			MescrollVue
		},
		methods: {
			enter (num){
						if(num==11){
							this.$router.push('/album1')
							
							this.$store.state.indexTap=10
						}else if(num==9){
							this.$router.push('/album2')
							this.$store.state.indexTap=1
						}else if(num==1){
							this.$router.push('/album3')
						}
			},
			cb(page, mescroll) {},
			scroll(mescroll, y, isUp) {
				console.log(mescroll);
				//           onScroll: this.scroll,
			},
			onloadimg() {
				this.$store.state.fixheight += this.$refs.topImg.offsetHeight;
				console.log(this.$store.state.fixheight);
			},
			message() {
				this.dialogVisible = true;
			},
			onlinenum() {
				//增加访问次数
				axios
					.get(this.$store.state.url + "/api/addusernum")
					.then(response => {})
					.catch(function(error) {
						// handle error
						console.log(error);
					});
			},
			reloadNum() {
				//查询访问次数
				axios
					.get(this.$store.state.url + "/api/getusernum")
					.then(response => {
						console.log(response);
						this.usernum = response.data.msg[0].onlinenum;
					})
					.catch(function(error) {
						// handle error
						console.log(error);
					});
			}
		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.title {
		text-align: left;
		font-size: 22px;
		/* font-weight: 900; */
		padding: 0.3125rem 0.9375rem 0.125rem 0.625rem;
		letter-spacing: 0.0625rem;
		font-weight: 900;
		line-height: 2.25rem;
		padding-top: 15px;
	}

	.inline-text {
		width: 100%;
		padding: 0.35rem 0.9375rem 0.3125rem 0.625rem;
		padding: 0.35rem 0.9375rem 22px 0.625rem;

		box-sizing: border-box;
		position: relative;
		overflow: hidden;
		font-size: 15px;
	}

	.inline-text div:nth-child(1) {
		float: left;
	}

	.inline-text div:nth-child(2) {
		float: right;
	}

	.overFlow {
		overflow: hidden;
	}

	.el-dialog .el-dialog__body {
		padding: 30px 20px;
		color: #606266;
		font-size: 14px;
		word-break: break-all;
		padding-bottom: 50px;
	}

	.enterBtn {
		margin: auto;
		padding: 0.3125rem 0.625rem 0.3125rem 0.625rem;
	}

	.enterBtn img {
		width: 100%;
		padding: 5px 30px;
		box-sizing: border-box;
	}

	/* .tap_tap div:nth-child(1){

}
.tap_tap div:nth-child(1):after{
    content: '';
    background: red;
    width: 90%;
    position: absolute;
    bottom: 0;
    height: 1px;
    left: 0;
} */
</style>



