<template>
  <div class="overFlow">
    <el-dialog :visible.sync="dialogVisible" width="80%" top="9vh">
      <img src="../assets/bgimg.jpg" alt width="100%" />
    </el-dialog>
    <!-- :up="mescrollUp" :down='mescrollDown' -->
    <div>
      <img ref="topImg" @load="onloadimg" src="../assets/9mouthtop.jpg" alt width="100%" />

      <div ref="toptitle">
        <h3 class="title">全国科普日北京主场活动（9月14-9月20）</h3>
      </div>

      <div class="inline-text" ref="toptext">
        <div style="text-align: left;">
          <span style="color: red;">{{usernum}}</span>
          <span>&nbsp;次照片浏览</span>
        </div>
        <div style="text-align: right;" @click="message">
          <!-- <span>活动主办</span> -->
        </div>
      </div>
      <!-- 切换组件 -->
      <Taptap2></Taptap2>
      <!-- 图片地图 -->
      <Imggroup1 v-if="$store.state.indexTap==1"></Imggroup1>
      <Imggroup2 v-if="$store.state.indexTap==2"></Imggroup2>
      <Imggroup3 v-if="$store.state.indexTap==3"></Imggroup3>
      <Imggroup4 v-if="$store.state.indexTap==4"></Imggroup4>
      <Imggroup5 v-if="$store.state.indexTap==5"></Imggroup5>
      <Imggroup6 v-if="$store.state.indexTap==6"></Imggroup6>
      <Imggroup7 v-if="$store.state.indexTap==7"></Imggroup7>
      <Imggroup8 v-if="$store.state.indexTap==8"></Imggroup8>
      <Imggroup9 v-if="$store.state.indexTap==9"></Imggroup9>
      <Imggroup10 v-if="$store.state.indexTap==10"></Imggroup10>
      <Imggroup11 v-if="$store.state.indexTap==11"></Imggroup11>

      <Footer></Footer>
    </div>
  </div>
</template>

<script>
import Taptap2 from "./taptap2.vue";
import Imggroup1 from "./imggroup_1.vue";
import Imggroup2 from "./imggroup_2.vue";
import Imggroup3 from "./imggroup_3.vue";
import Imggroup4 from "./imggroup_4.vue";
import Imggroup5 from "./imggroup_5.vue";
import Imggroup6 from "./imggroup_6.vue";
import Imggroup7 from "./imggroup_7.vue";
import Imggroup8 from "./imggroup_8.vue";

import Imggroup9 from "./imggroup_9.vue";
import Imggroup10 from "./imggroup_10.vue";
import Imggroup11 from "./imggroup_11.vue";

import Footer from "./footer.vue";
import MescrollVue from "mescroll.js/mescroll.vue";
const axios = require("axios");
import qs from "qs";
//  mescrollUp: {
//     callback: this.cb,
//     htmlLoading: '<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载11中..</p>',
//     htmlNodata: '<p class="upwarp-nodata">-- 没有数据了 --</p>',
//     onScroll: this.scroll
//   },
//   mescrollDown: {
//     use: false,
//   },
export default {
  data() {
    return {
      usernum: 0,
      list: [],
      imgdata: [
        {
          url: "",
          txt: "礼赞共和国  智慧新生活    礼赞共和国  智慧新生活",
          datetime: "2019-08-12 24:24:24",
          likes: "0",
          id: 0
        }
      ],

      dialogVisible: false
    };
  },
  created() {
    this.onlinenum();
    this.reloadNum();
  },
  mounted() {
    // 监听dom渲染完成
    this.$nextTick(function() {
      // 这里fixedHeaderRoot是吸顶元素的ID
      this.$store.state.fixheight += this.$refs.toptitle.offsetHeight;
      console.log(this.$store.state.fixheight);

      this.$store.state.fixheight += this.$refs.toptext.offsetHeight;
      console.log(this.$store.state.fixheight);
    });
  },
  components: {
    Taptap2,
    Footer,
    Imggroup11,
    Imggroup10,
    Imggroup9,
    Imggroup8,
    Imggroup7,
    Imggroup6,
    Imggroup5,
    Imggroup4,
    Imggroup3,
    Imggroup2,
    Imggroup1,
    MescrollVue
  },
  methods: {
    cb(page, mescroll) {},
    scroll(mescroll, y, isUp) {
      console.log(mescroll);
      //           onScroll: this.scroll,
    },
    onloadimg() {
      this.$store.state.fixheight += this.$refs.topImg.offsetHeight;
      console.log(this.$store.state.fixheight);
    },
    message() {
      this.dialogVisible = true;
    },
    onlinenum() {
      //增加访问次数
      axios
        .get(this.$store.state.url + "/api/addusernum")
        .then(response => {})
        .catch(function(error) {
          // handle error
          console.log(error);
        });
    },
    reloadNum() {
      //查询访问次数
      axios
        .get(this.$store.state.url + "/api/getusernum")
        .then(response => {
          console.log(response);
          this.usernum = response.data.msg[0].onlinenum;
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title {
		text-align: left;
		font-size: 15px;
		/* font-weight: 900; */
		padding: 0.875rem;
		letter-spacing: 0.0625rem;
		line-height: 24px;
		padding-top: 5px;
}

.inline-text {
  width: 100%;
  padding: 0.3125rem 0.9375rem 0.3125rem 0.625rem;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}

.inline-text div:nth-child(1) {
  float: left;
}

.inline-text div:nth-child(2) {
  float: right;
}
.overFlow {
  overflow: hidden;
  background: #fff;
}

.el-dialog .el-dialog__body {
  padding: 30px 20px;
  color: #606266;
  font-size: 14px;
  word-break: break-all;
  padding-bottom: 50px;
}

/* .tap_tap div:nth-child(1){

}
.tap_tap div:nth-child(1):after{
    content: '';
    background: red;
    width: 90%;
    position: absolute;
    bottom: 0;
    height: 1px;
    left: 0;
} */
</style>



