<template>
	<div>
		<div class="overflow">
			<div class="tap_tap fix_top" v-if="headerFixed ">
				<div :class="{active_tap:10==$store.state.indexTap}" @click="activeTap(10)">2019第七届北京科技电影展</div>
				<div :class="{active_tap:11==$store.state.indexTap}" @click="activeTap(11)">世界科普展示交流活动</div>
				<div :class="{active_tap:9==$store.state.indexTap}" @click="activeTap(9)">学术月</div>
				<div :class="{active_tap:12==$store.state.indexTap}" @click="activeTap(12)">北京国际科学节圆桌会议 </div>
				<div :class="{active_tap:13==$store.state.indexTap}" @click="activeTap(13)">中外科技馆馆长对话</div>
				<div :class="{active_tap:14==$store.state.indexTap}" @click="activeTap(14)">主旨报告</div>
				<!--<div :class="{active_tap:10==$store.state.indexTap}" @click="activeTap(10)">待定2</div>
        <div :class="{active_tap:11==$store.state.indexTap}" @click="activeTap(11)">待定3</div>-->
			</div>

			<div class="tap_tap" ref="TapTap">
				<div :class="{active_tap:10==$store.state.indexTap}" @click="activeTap(10)">2019第七届北京科技电影展</div>
				<div :class="{active_tap:11==$store.state.indexTap}" @click="activeTap(11)">世界科普展示交流活动</div>
				<div :class="{active_tap:9==$store.state.indexTap}" @click="activeTap(9)">学术月</div>
				<div :class="{active_tap:12==$store.state.indexTap}" @click="activeTap(12)">北京国际科学节圆桌会议 </div>
				<div :class="{active_tap:13==$store.state.indexTap}" @click="activeTap(13)">中外科技馆馆长对话</div>
				<div :class="{active_tap:14==$store.state.indexTap}" @click="activeTap(14)">主旨报告</div>
				<!--   <div :class="{active_tap:10==$store.state.indexTap}" @click="activeTap(10)">待定2</div>
        <div :class="{active_tap:11==$store.state.indexTap}" @click="activeTap(11)">待定3</div>-->
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				list: [],
				headerFixed: false,
				offsetHeight: "",
				offsetHeight: ""
			};
		},
		mounted() {
			// 监听dom渲染完成
			this.$nextTick(function() {
				// 这里fixedHeaderRoot是吸顶元素的ID
				let header = this.$refs.TapTap;
				// 这里要得到top的距离和元素自身的高度
				this.offsetTop = header.offsetTop;
				this.offsetHeight = header.offsetHeight;
				console.log("offsetTop:" + this.offsetTop + "," + this.offsetHeight);
				// this.$store.state.fixheight +=this.offsetHeight/2
			});

			window.addEventListener("scroll", this.handleScroll);
		},
		destroyed() {
			window.removeEventListener("scroll", this.handleScroll);
		},
		methods: {
			activeTap: function(num) {
				this.$store.state.indexTap = num;
			},
			handleScroll() {
				// 得到页面滚动的距离
				let scrollTop =
					window.pageYOffset ||
					document.documentElement.scrollTop ||
					document.body.scrollTop;
				// 判断页面滚动的距离是否大于吸顶元素的位置
				this.headerFixed = scrollTop > this.$store.state.fixheight;
			}
		}
	};
</script>

<!-- (this.offsetTop - this.offsetHeight * 2) -->

<style scoped>
	.aoverflow {
		overflow-x: scroll;
	}

	.tap_tap {
		overflow: hidden;
		box-sizing: border-box;
		padding: 0.625rem 0.625rem;
		color: rgba(0, 0, 0, 0.5);
		width: 100%;
		background: white;
		overflow: hidden;
	}

	.tap_tap div {
		float: left;
		height: 1.875rem;
		line-height: 1.875rem;
		font-size: 0.6rem;
		position: relative;
		font-style: normal;
		margin-right: 0.75rem;
		overflow: hidden;
		text-align: justify;
	}

	/*

*/

	.active_tap {
		color: rgba(0, 0, 0, 1);
		font-style: normal;
	}

	.active_tap:after {
		content: "";
		background: red;
		width: 100%;
		position: absolute;
		bottom: 0;
		height: 0.0625rem;
		left: 0;
	}

	.fix_top {
		position: fixed;
		top: 0;
		z-index: 99;
	}
</style>



